body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --base-medium: Poppins;
  --font-yantramanav: Yantramanav;
  --font-font-awesome-5-free: "Font Awesome 5 Free";
  --text-base-font-normal: Inter;

  /* font sizes */
  --text-base-font-normal-size: 16px;
  --font-size-mini: 15px;
  --font-size-5xl: 24px;
  --font-size-3xl: 22px;
  --font-size-lg: 18px;
  --uppercase-text-sm-font-semibold-size: 14px;

  /* Colors */
  --color-white1: #fff;
  --color-royalblue-100: #4776e6;
  --color-dimgray-100: #49596d;
  --color-dimgray-200: rgba(73, 89, 109, 0.3);
  --color-gray-100: #888;
  --color-gray-200: #757575;
  --gray-900: #111928;
  --color-slateblue: #706fe5;
  --main-color-1: #1e1f4b;
  --greys-blue-grey-900: #151d48;
  --greys-blue-grey-300: #737791;
  --color-slategray-100: #6b7280;

  /* Gaps */
  --gap-xl: 20px;
  --gap-base: 16px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-21xl: 40px;

  /* Border radiuses */
  --br-7xs: 6px;
  --br-31xl: 50px;
  --br-11xl: 30px;
  --br-xs: 12px;
  --br-base: 16px;
}



@keyframes fade-in {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes wobble-hor-bottom {
  0%,
  to {
    transform: translateX(0);
    transform-origin: 50% 50%;
  }
  15% {
    transform: translateX(-30px) rotate(-6deg);
  }
  30% {
    transform: translateX(15px) rotate(6deg);
  }
  45% {
    transform: translateX(-15px) rotate(-3.6deg);
  }
  60% {
    transform: translateX(9px) rotate(2.4deg);
  }
  75% {
    transform: translateX(-6px) rotate(-1.2deg);
  }
}
@keyframes tilt-in-top-1 {
  0% {
    transform: rotateY(30deg) translateY(-300px) skewY(-30deg);
    opacity: 0;
  }
  to {
    transform: rotateY(0deg) translateY(0) skewY(0deg);
    opacity: 1;
  }
}
@keyframes shadow-inset-center {
  0% {
    box-shadow: inset 0 0 0 0 transparent;
  }
  to {
    box-shadow: inset 0 0 14px 0 rgb(0 0 0/50%);
  }
}
@keyframes shadow-drop-bottom {
  0% {
    box-shadow: 0 0 0 0 transparent;
  }
  to {
    box-shadow: 0 12px 20px -12px rgb(0 0 0/35%);
  }
}
@keyframes scale-up {
  0% {
    transform: scale(0.5);
  }
  to {
    transform: scale(1);
  }
}








